// 
// 
export function debounce(func,delay=100){
    let timer = null // 
    return function(...args){ // func
      if(timer) clearTimeout(timer) // 
      timer = setTimeout(()=>{
        func.apply(this,args) // funcargs
      },delay)
    }
  }

  // 
  // new Date(value * 1000).toLocaleString()  //  yyyy/MM/dd  hh:mm:ss
export function formatDate(date, fmt) {
  // 1. y+ y
  if(/(y+)/.test(fmt)) {
    // RegExp.$1 /(y+)/(date.getFullYear() + '')...
    // (date.getFullYear() + '')
    // substr 4-20192019
    fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length));
  }
  let o = {
    'M+': date.getMonth() + 1,
    'd+': date.getDate(),
    'h+': date.getHours(),
    'm+': date.getMinutes(),
    's+': date.getSeconds()
  };
  for (let k in o) {
    if (new RegExp(`(${k})`).test(fmt)) {
      let str = o[k] + '';
      fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? str : padLeftZero(str));
    }
  }
  return fmt;
}
//  0102
// str 04 0004.substr(2) -> 04
// if str = 4  -> 004.sbustr(1)  ->  04
function padLeftZero (str) {
  return ('00' + str).substr(str.length);
}